<template>
  <div id="app">

    <div class="container">
      <input type="search" v-model="searchText" id="search" autofocus autocomplete="off" placeholder="Upos Code Search">

      <div id="results" v-if="searchText">
        <p v-for="item in searchHandler" :key="item.code">
          {{ item.code }} - {{ item.description }}
        </p>
      </div>

    </div>

  </div>
</template>

<script>
import { errors } from './data/errors';
export default {
  name: 'App',
  data() {
    return {
      searchText: '',
      data: []
    };
  },
  created() {
    this.data = errors;
  },
  computed: {
    searchHandler(){
      return this.data.filter(elem => {
        return elem.code.toLowerCase().includes(this.searchText);
      });
    }
  }
};
</script>

